<template>
  <div id="quiz">
    <head-top title="截屏提问"></head-top>
    <van-pull-refresh v-model="isLoading" @refresh="getQuestionData" class="pull_fresh">
      <div class="question-content">
        <div class="item" @click="selectQuestion(item)" v-for="(item, k) in questionList" :key="k">
          <div class="bg fL">
            <img :src="item.img" />
          </div>
          <div class="details fR">
            <div
              class="info"
            >{{item.SSSerial + '.[' + getQuestionTypeName(item.SSType) + '] ' + item.SSScore + '分'}}</div>
            <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
          </div>
          <span
            class="prompt"
            :class="item.SSPattern=== 3 ? 'red':'green'"
          >{{getPatternName(item.SSPattern)}}</span>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Tab, Tabs } from "vant";

import { screenQuestionTypeName, screenPatternName } from "@/utils/enum";
export default {
  name: "",
  components: { headTop, [Tab.name]: Tab, [Tabs.name]: Tabs },
  props: {},
  data() {
    return {
      questionList: [],
      isLoading: false
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getQuestionData();
    });
  },
  methods: {
    getQuestionData() {
      this.questionList = [];
      this.isLoading = true;
      this.$mainHttp.screenQustion
        .getList({
          courseID: this.$store.getters.course["CourseID"],
          ssType: this.$route.params.type
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.questionList.push({
                ...item,
                img: this.$mainHttp.resourse.picture(item["SSContent"], 1)
              });
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    getQuestionTypeName(val) {
      return screenQuestionTypeName(val);
    },
    getPatternName(val) {
      return screenPatternName(val);
    },
    selectQuestion(item) {
      this.$router.push("/history/screenQuestionInfo/" + item["SSID"]);
    }
  }
};
</script>

<style lang='less' scoped>
#quiz {
  .question-content {
    margin-top: 10px;
    width: 100%;
    padding-bottom: 2vh;
    background: rgba(255, 255, 255, 1);
      max-height: calc(100vh - 4.5rem - 10px);
    overflow-y: auto;
    .item {
      height: 12vh;
      width: 93vw;
      margin: 0 auto;
      overflow: hidden;
      position: relative;
      border-bottom: 1px solid rgba(221, 221, 221, 1);
      .bg {
        width: 20vw;
        height: 9vh;
        margin: 1vh;
        margin-left: 0px;
        text-align: center;
        img {
          max-width: 20vw;
          max-height: 9vh;
        }
      }
      .details {
        text-align: left;
        width: 68vw;
        height: 10vh;
        margin-top: 1vh;
      }
      .info {
        font-size: 1.4rem;
        font-family: MicrosoftYaHei;
        color: rgba(51, 51, 51, 1);
        line-height: 15px;
        margin-bottom: 30px;
      }
      .time {
        font-size: 1.2rem;
        font-family: MicrosoftYaHei;
        color: rgba(102, 102, 102, 1);
      }
      .prompt {
        position: absolute;
        top: 15px;
        right: 0;
      }
      .red {
        color: #e97b7b;
      }
      .green {
        color: #00cbab;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem - 10px);
    }
  }
}
</style>


